import { useSearchParams } from "react-router-dom"
import { useAuth } from "../../Hooks/useAuth"
import { useEffect, useState } from "react"
import { get } from "../../Functions/Api"

const ReadSurat = () => {
    const { token_access } = useAuth()
    const [params] = useSearchParams()
    const nama_file = params.get("nama_file")
    console.log(nama_file)
    const [surat, setSurat] = useState<string>()

    useEffect(() => {
        const getSuratPengantar = async () => {
            const res = await get(
                `surat/file?file_name=${nama_file}&${token_access}`
            )
            console.log(res.data.data)
            if (res.ok) {
                setSurat(res.data.data)
            }

            return res
        }
        getSuratPengantar()
    }, [nama_file, token_access])

    return (
        <div
            className="text-center"
            style={{ height: "100vh", overflowX: "hidden" }}
        >
            {surat && (
                <embed
                    src={`data:application/pdf;base64,${surat}`}
                    id="surat-pengantar"
                    width="100%"
                    height="100%"
                    style={{ overflowX: "unset" }}
                />
            )}
        </div>
    )
}

export default ReadSurat
